import React, { Component } from 'react';
import Quest from './Quest';

class Quests extends Component {
    state = {}
    render() { 
        return (
            <React.Fragment>
                <div id="quests">
                    <div className="content">
                        <h1>Lista zadań</h1>
                        <br />
                        <div className="content-quests">
                            <Quest />
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
 
export default Quests;