import React, { Component } from 'react';
import * as quests from '../data/quests.json';

class Quest extends Component {
    state = {
        acviteQuests:[
            quests.InnKey
        ]
    }

    render() {
        return (
            <React.Fragment>
                <div className="content-quests_item">
                    {this.state.acviteQuests.map(m => 
                        <div key={m[0]} className="quest">
                            <div className="quest-name">
                                <h2 onClick={this.handleShowDesc}>{m[0]}</h2>
                            </div>
                            <div className="quest-desc active">
                                {m[1]}
                            </div>
                        </div>
                    )}
                </div>
            </React.Fragment>
        );
    }
}
 
export default Quest;