import React, { Component } from 'react';
import sounds from '../audio/bard';
import * as items from '../data/items.json';
import * as weapons from '../data/weapons.json';

class Item extends Component {
    state = {
        gold: 70,
        equipment:[
            items.QuestItems.IsmenaBook,
            items.QuestItems.RoomKey,
            items.Items.HealthPotions,
            items.Items.Bag,
            items.Items.FoodRations,
            weapons.OneHanded.Dagger
        ]
    }

    handlePlaySound = id => {
        const { item, potion, book, gold, dagger } = sounds;
        switch(id) {
            case 'item':
                item.play();
                break;
            case 'gold':
                gold.play();
                break;
            case 'potion':
                potion.play();
                break;
            case 'book':
                book.play();
                break;
            case 'dagger':
                dagger.play();
                break;
            default:
                item.play();
        }
    }

    render() { 
        return (
            <div className="content-equipment_item">
                {this.state.equipment.map(m =>
                    <div key={m[0]} className="item">
                        <div className="item-name">
                            <h2>{m[0]}</h2>
                        </div>
                        <div className="item-desc">
                            <p>Waga: {m[1]}</p>
                            <p>Wartość: {m[2]}</p>
                            <p>Ilość: {m[5]}</p>
                            <p>Opis: {m[3]}</p>
                        </div>
                        <div className="item-buttons">
                            <button onClick={() => this.handlePlaySound(m[4])}>Użyj</button>
                            <button>Wyrzuć</button>
                        </div>
                    </div>
                )}
            </div>
        );
    }
}
 
export default Item;