import React, { Component } from 'react';
import $ from 'jquery';
import sounds from '../audio/bard';

class Infobar extends Component {
    state = {  }

    handleShowInfobar = () => {
        if ($('#infobar').hasClass('active') === true) {
            $('#infobar').removeClass('active');
        } else {
            $('#infobar').addClass('active');
        }
        sounds.pageFlip.play();
    }

    handleHideInfobar = () => {
        sounds.pageFlip.play();
        $('#infobar').removeClass('active');
        $('#infobar').addClass('hidden');
    }

    render() { 
        return (
            <div id="infobar" className="hidden" onLoad={this.props.onShowQuest()}>
                <p>Dodano nowe zadanie: <b><a href="#quests" onClick={this.handleHideInfobar}>Klucz od pokoju</a></b><i  onClick={this.handleShowInfobar} className="bi bi-caret-right"></i></p>
            </div>
        );
    }
}
 
export default Infobar;