import React, { Component } from 'react';
import $ from 'jquery';
import * as character from '../data/character.json';

class Sidebar extends Component {
    state = {

    }

    handleShowSidebar = () => {
        if($('.sideButton').hasClass('active') === true) {
            $('.sideButton').removeClass('active');
            $('.sidebar').removeClass('active');
        } else {
            $('.sideButton').addClass('active');
            $('.sidebar').addClass('active');
        }
    }

    render() { 
        return ( 
            <React.Fragment>
                <div className="sideButton">
                    <h1 onClick={this.handleShowSidebar}><i className="bi bi-caret-right-square"></i></h1>
                </div>
                <div className="sidebar">
                    <div className="sidebar-bars">
                        <div className="sidebar-bars_item">
                            <h4>Punkty wytrzymałości:</h4>
                            <p>{character.HP}/{character.maxHP}</p>
                        </div>
                        <div className="sidebar-bars_item">
                            <h4>Doświadczenie:</h4>
                            <p>{character.exp}/{character.nextLvl}</p>
                        </div>
                        <div className="sidebar-bars_item">
                            <h4>Udźwig postaci:</h4>
                            <p>4.25 / {character.maxWeight} kg</p>
                        </div>
                        <div className="sidebar-bars_effects">
                            <h4>Aktywne efekty:</h4>
                            <p>{character.activeEffects[0]}</p>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
 
export default Sidebar;