import React, { Component } from 'react';
import Item from './Item';

class Equipment extends Component {
    state = {}

    render() {
        return (
            <React.Fragment>
                <div id="equipment">
                    <div className="content">
                        <h1>Ekwipunek</h1>
                        <br />
                        <div className="content-equipment">
                            <Item />
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
 
export default Equipment;