import React, { Component } from 'react';
import sounds from '../audio/bard';
import Equipment from './Equipment';
import Navbar from './Navbar';
import Story from './Story';
import WorldMap from './WorldMap';
import $ from 'jquery';
import Hero from './Hero';
import Quests from './Quests';
import GoUp from './GoUp';
import Infobar from './Infobar';

class Game extends Component {
    state = {
        play: 0,
        music: [
            "./music/track_01.mp3",
            "./music/track_02.mp3",
            "./music/track_03.mp3",
            "./music/track_04.mp3",
            "./music/track_05.mp3",
            "./music/track_06.mp3",
            "./music/track_07.mp3"
        ]
    }

    handleShowNewQuest = () => {
        setTimeout(() => {
            $('#infobar').removeClass('hidden');
            $('#infobar').addClass('active');
            sounds.pageFlip.play();
            
            setTimeout(() => {
                if($('#infobar').hasClass('active') === true) {
                    $('#infobar').removeClass('active');
                    sounds.pageFlip.play();
                }
            }, 5000);
        }, 5000);
    }

    render() {
        const { play, music } = this.state;
        return (
            <React.Fragment>
                <header>
                    <Navbar />
                </header>
                <main>
                    <Infobar onClick={this.handleShowInfobar} onShowQuest={this.handleShowNewQuest} />
                    <div className="container">
                        <Story />
                        <WorldMap />
                        <Equipment />
                        <Hero />
                        <Quests />
                    </div>
                    <GoUp />
                    <audio src={music[play]} autoPlay controls loop></audio>
                </main>
            </React.Fragment>
        );
    }
}
 
export default Game;